module.exports = [{"route_id":"1","agency_id":"MTA NYCT","route_short_name":"1","route_long_name":"Broadway - 7 Avenue Local","route_desc":"Trains operate between 242 St in the Bronx and South Ferry in Manhattan, most times","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t1cur.pdf","route_color":"EE352E","route_text_color":null},{"route_id":"2","agency_id":"MTA NYCT","route_short_name":"2","route_long_name":"7 Avenue Express","route_desc":"Trains operate between Wakefield-241 St, Bronx, and Flatbush Av-Brooklyn College, Brooklyn, at all times. Trains operate local in Bronx and Brooklyn. Trains operate express in Manhattan except late night when it operates local.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t2cur.pdf","route_color":"EE352E","route_text_color":" "},{"route_id":"3","agency_id":"MTA NYCT","route_short_name":"3","route_long_name":"7 Avenue Express","route_desc":"Trains operate between 148 St, 7 Av, Manhattan, and New Lots Av, Brooklyn, at all times except late nights. During late nights, trains operate only in Manhattan between 148 St, 7 Av and Times Square-42 St.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t3cur.pdf","route_color":"EE352E","route_text_color":null},{"route_id":"4","agency_id":"MTA NYCT","route_short_name":"4","route_long_name":"Lexington Avenue Express","route_desc":"Trains operate daily between Woodlawn/Jerome Av, Bronx, and Utica Av/Eastern Pkwy, Brooklyn, running express in Manhattan and Brooklyn. During late night and early morning hours, trains runs local in Manhattan and Brooklyn, and extends beyond Utica Av to New Lots/Livonia Avs, Brooklyn.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t4cur.pdf","route_color":"00933C","route_text_color":null},{"route_id":"5","agency_id":"MTA NYCT","route_short_name":"5","route_long_name":"Lexington Avenue Express","route_desc":"Weekdays daytime, most trains operate between either Dyre Av or 238 St-Nereid Av, Bronx, and Flatbush Av-Brooklyn College, Brooklyn. At all other times except during late nights, trains operate between Dyre Av, Bronx, and Bowling Green, Manhattan. During late nights trains operate only in the Bronx between Dyre Av and E 180 St/MorrisPark Av. Customers who ride during late night hours can transfer to 2 service at the E 180 St Station. At all times, trains operate express in Manhattan and Brooklyn. Weekdays, trains in the Bronx operate express from E 180 St to 149 St-3 Av during morning rush hours (from about 6 AM to 9 AM), and from 149 St-3 Av to E 180 St during the evening rush hours (from about 4 PM to 7 PM).","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t5cur.pdf","route_color":"00933C","route_text_color":null},{"route_id":"5X","agency_id":"MTA NYCT","route_short_name":"5X","route_long_name":"Lexington Avenue Express","route_desc":"Weekdays daytime, most trains operate between either Dyre Av or 238 St-Nereid Av, Bronx, and Flatbush Av-Brooklyn College, Brooklyn. At all other times except during late nights, trains operate between Dyre Av, Bronx, and Bowling Green, Manhattan. During late nights trains operate only in the Bronx between Dyre Av and E 180 St/MorrisPark Av. Customers who ride during late night hours can transfer to 2 service at the E 180 St Station. At all times, trains operate express in Manhattan and Brooklyn. Weekdays, trains in the Bronx operate express from E 180 St to 149 St-3 Av during morning rush hours (from about 6 AM to 9 AM), and from 149 St-3 Av to E 180 St during the evening rush hours (from about 4 PM to 7 PM).","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t5cur.pdf","route_color":"00933C","route_text_color":null},{"route_id":"6","agency_id":"MTA NYCT","route_short_name":"6","route_long_name":"Lexington Avenue Local","route_desc":"Local trains operate between Pelham Bay Park/Bruckner Expwy, Bronx, and Brooklyn Bridge/City Hall, Manhattan, at all times.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t6cur.pdf","route_color":"00933C","route_text_color":null},{"route_id":"6X","agency_id":"MTA NYCT","route_short_name":"6X","route_long_name":"Lexington Avenue Express","route_desc":"Express trains operate between Pelham Bay Park/Bruckner Expwy, Bronx, and Brooklyn Bridge/City Hall, Manhattan, weekday mornings toward Manhattan. Weekday afternoons and evenings, these trains operate express to the Bronx.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t6cur.pdf","route_color":"00A65C","route_text_color":null},{"route_id":"7","agency_id":"MTA NYCT","route_short_name":"7","route_long_name":"Flushing Local","route_desc":"Trains operate between Main St-Flushing, Queens, and 11th Av-34th St, at all times. ","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t7cur.pdf","route_color":"B933AD","route_text_color":null},{"route_id":"7X","agency_id":"MTA NYCT","route_short_name":"7X","route_long_name":"Flushing Express","route_desc":"Trains operate between Main St-Flushing, Queens, and 11th Av-34th St, Manhattan, weekday mornings toward Manhattan. Weekday afternoons and evenings, these trains operate express to Queens.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t7cur.pdf","route_color":"B933AD","route_text_color":null},{"route_id":"GS","agency_id":"MTA NYCT","route_short_name":"S","route_long_name":"42 St Shuttle","route_desc":"Operates in Manhattan between Grand Central and Times Square. The shuttle provides a free transfer between 4, 5, 6, and 7 service at Grand Central-42 St and A, C, E, N, Q, R, W, 1, 2, 3, and 7 service at Times Square-42 St. The shuttle runs at all times except during late nights, from about 12 midnight to 6 AM.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/t0cur.pdf","route_color":"6D6E71","route_text_color":null},{"route_id":"A","agency_id":"MTA NYCT","route_short_name":"A","route_long_name":"8 Avenue Express","route_desc":"Trains operate between Inwood-207 St, Manhattan and Far Rockaway-Mott Avenue, Queens at all times. Also from about 6 AM until about midnight, additional trains operate between Inwood-207 St and Lefferts Boulevard (trains typically alternate between Lefferts Blvd and Far Rockaway). During weekday morning rush hours, special trains, denoted in the schedule by a diamond symbol, operate from Rockaway Park-Beach 116 St, Queens, toward Manhattan. These trains make local stops between Rockaway Park and Broad Channel. Similarly, in the evening rush hour special trains leave Manhattan operating toward Rockaway Park-Beach 116 St, Queens.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tacur.pdf","route_color":"2850AD","route_text_color":"FFFFFF"},{"route_id":"B","agency_id":"MTA NYCT","route_short_name":"B","route_long_name":"6 Avenue Express","route_desc":"Trains operate, weekdays only, between 145 St, Manhattan, and Brighton Beach, Brooklyn at all times except late nights. The route extends to Bedford Park Blvd, Bronx, during rush hours.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tbcur.pdf","route_color":"FF6319","route_text_color":null},{"route_id":"C","agency_id":"MTA NYCT","route_short_name":"C","route_long_name":"8 Avenue Local","route_desc":"Trains operate between 168 St, Manhattan, and Euclid Av, Brooklyn, daily from about 6 AM to 11 PM.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tccur.pdf","route_color":"2850AD","route_text_color":"FFFFFF"},{"route_id":"D","agency_id":"MTA NYCT","route_short_name":"D","route_long_name":"6 Avenue Express","route_desc":"Trains operate, at all times, from 205 Street, Bronx, to Stillwell Avenue, Brooklyn via Central Park West and 6th Avenue in Manhattan, and via the Manhattan Bridge to and from Brooklyn. When in Brooklyn trains operate via 4th Avenue then through Bensonhurst to Coney Island. Trains typically operate local in the Bronx, express in Manhattan, and local in Brooklyn. But please note that Bronx rush hour trains operate express (peak direction ONLY), and Brooklyn trains operate express along the 4th Avenue segment (all times except late nights).","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tdcur.pdf","route_color":"FF6319","route_text_color":null},{"route_id":"E","agency_id":"MTA NYCT","route_short_name":"E","route_long_name":"8 Avenue Local","route_desc":"Trains operate between Jamaica Center (Parsons/Archer), Queens, and World Trade Center, Manhattan, at all times.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tecur.pdf","route_color":"2850AD","route_text_color":"FFFFFF"},{"route_id":"F","agency_id":"MTA NYCT","route_short_name":"F","route_long_name":"Queens Blvd Express/ 6 Av Local","route_desc":"Trains operate at all times between Jamaica, Queens, and Stillwell Av, Brooklyn via the 63 St Connector (serving 21 St-Queensbridge, Roosevelt Island, Lexington Av-63 St, and 57 St-6 Av). F trains operate express along Queens Blvd at all times. Trains do not serve Queens Plaza, 23 St-Ely Av, Lexington Av-53 St and 5 Av-53 St.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tfcur.pdf","route_color":"FF6319","route_text_color":null},{"route_id":"FS","agency_id":"MTA NYCT","route_short_name":"S","route_long_name":"Franklin Avenue Shuttle","route_desc":"Train provides full time connecting service between the A and C at the Franklin Av/Fulton St station, and the Q at the Prospect Park/Empire Blvd station. A free transfer is also available at the Botanic Garden/Eastern Parkway Station to the 2, 3, 4, and 5 service.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tscur.pdf","route_color":null,"route_text_color":null},{"route_id":"G","agency_id":"MTA NYCT","route_short_name":"G","route_long_name":"Brooklyn-Queens Crosstown","route_desc":"Trains operate between Court Square, Queens and Church Av, Brooklyn weekdays, and all weekend. Weekday evenings and late nights, trains operate between Forest Hill-71 Av, Queens and Church Av, Brooklyn.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tgcur.pdf","route_color":"6CBE45","route_text_color":null},{"route_id":"J","agency_id":"MTA NYCT","route_short_name":"J","route_long_name":"Nassau St Local","route_desc":"Trains operate weekdays between Jamaica Center (Parsons/Archer), Queens, and Broad St, Manhattan. During weekdays, Trains going to Manhattan run express in Brooklyn between Myrtle Av and Marcy Av from about 7 AM to 1 PM and from Manhattan from 1:30 PM and 8 PM. During weekday rush hours, trains provide skip-stop service. Skip-stop service means that some stations are served by J trains, some stations are served by the Z trains, and some stations are served by both J and Z trains. J/Z skip-stop service runs towards Manhattan from about 7 AM to 8:15 AM and from Manhattan from about 4:30 PM to 5:45 PM.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tjcur.pdf","route_color":"996633","route_text_color":null},{"route_id":"L","agency_id":"MTA NYCT","route_short_name":"L","route_long_name":"14 St-Canarsie Local","route_desc":"Trains operate between 8 Av/14 St, Manhattan, and Rockaway Pkwy/Canarsie, Brooklyn, 24 hours daily.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tlcur.pdf","route_color":"A7A9AC","route_text_color":null},{"route_id":"M","agency_id":"MTA NYCT","route_short_name":"M","route_long_name":"QNS BLVD-6th AVE/ Myrtle Local","route_desc":"Trains operate between Middle Village-Metropolitan Avenue, Queens and Myrtle Avenue, Brooklyn at all times. Service is extended weekdays (except late nights) Continental Ave, Queens, All trains provide local service.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tmcur.pdf","route_color":"FF6319","route_text_color":null},{"route_id":"N","agency_id":"MTA NYCT","route_short_name":"N","route_long_name":"Broadway Local","route_desc":"Trains operate from Ditmars Boulevard, Queens, to Stillwell Avenue, Brooklyn, at all times. N trains in Manhattan operate along Broadway and across the Manhattan Bridge to and from Brooklyn. Trains in Brooklyn operate along 4th Avenue, then through Borough Park to Gravesend. Trains typically operate local in Queens, and either express or local in Manhattan and Brooklyn, depending on the time. Late night trains operate via Whitehall Street, Manhattan. Late night service is local.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tncur.pdf","route_color":"FCCC0A","route_text_color":null},{"route_id":"Q","agency_id":"MTA NYCT","route_short_name":"Q","route_long_name":"Broadway Express","route_desc":"Trains operate between Astoria, Queens, and Stillwell Av, Brooklyn except nights. During late nights trains will operate from 57th Street/7th Avenue, Manhattan, and Stillwell Av, Brooklyn. Trains operate express from 34th St. via Broadway to Canal Street, except late nights when trains operate express 34th to Canal(lower level), cross into Brooklyn via Manhattan Bridge, then operate local to and from Stillwell Av.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tqcur.pdf","route_color":"FCCC0A","route_text_color":null},{"route_id":"R","agency_id":"MTA NYCT","route_short_name":"R","route_long_name":"Broadway Local","route_desc":"Trains operate local between Forest Hills-71 Av, Queens, and 95 St/4 Av, Brooklyn, at all times except late nights. During late nights, trains operate only in Brooklyn between 36 St and 95 St/4 Av.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/trcur.pdf","route_color":"FCCC0A","route_text_color":null},{"route_id":"H","agency_id":"MTA NYCT","route_short_name":"S","route_long_name":"Rockaway Park Shuttle","route_desc":"Service operates at all times between Broad Channel, and Rockaway Park, Queens.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/thcur.pdf","route_color":null,"route_text_color":null},{"route_id":"W","agency_id":"MTA NYCT","route_short_name":"W","route_long_name":"Broadway Local","route_desc":"Trains operate from Ditmars Boulevard, Queens, to Stillwell Avenue, Brooklyn, at all times. N trains in Manhattan operate along Broadway and across the Manhattan Bridge to and from Brooklyn. Trains in Brooklyn operate along 4th Avenue, then through Borough Park to Gravesend. Trains typically operate local in Queens, and either express or local in Manhattan and Brooklyn, depending on the time. Late night trains operate via Whitehall Street, Manhattan. Late night service is local.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/twcur.pdf","route_color":"FCCC0A","route_text_color":null},{"route_id":"Z","agency_id":"MTA NYCT","route_short_name":"Z","route_long_name":"Nassau St Express","route_desc":"During weekday rush hours, J and Z trains provide skip-stop service. Skip-stop service means that some stations are served by J trains, some stations are served by the Z trains, and some stations are served by both J and Z trains. J/Z skip-stop service runs towards Manhattan from about 7 AM to 8:15 AM and from Manhattan from about 4:30 PM to 5:45 PM.","route_type":1,"route_url":"http://web.mta.info/nyct/service/pdf/tjcur.pdf","route_color":"996633","route_text_color":null},{"route_id":"SI","agency_id":"MTA NYCT","route_short_name":"SIR","route_long_name":"Staten Island Railway","route_desc":"Service runs 24 hours a day between the St George and Tottenville terminals. At the St George terminal, customers can make connections with Staten Island Ferry service to Manhattan.","route_type":2,"route_url":"http://web.mta.info/nyct/service/pdf/sircur.pdf","route_color":null,"route_text_color":null}]

